body,#root
    margin: 0
    padding: 0
    width: 100%
    height: 100% 
    overflow: hidden 

.magic-cursor
    z-index: 100
    border-radius: 50%
    position: absolute
    width: 117px
    height: 93px
    transition: 0.1s
    background: url('./media/images/magic-cursor.png')

.loading-page
    z-index: 1000
    position: absolute
    background: red

.starting-page
    z-index:25
    position: absolute
    background: #bf6b30

.starting-page-label
    z-index: 27
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    font-size: 7rem
    font-weight: bold
    color: white
    opacity: 0.8
    transition: transform 1s

.starting-page-image
    z-index: 26
    position: absolute
    transition: opacity 2s
    background: url(./media/images/starting-page-background.png) no-repeat center

.content-page
    z-index: -5
    display: flex
    flex-direction: row

.content-page section
    z-index: 1
    transition: transform 0.7s
    user-select: none
